<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div @click="showDialog()" class="btn-main">导入</div>
        <!--<div @click="$router.push('/edu-affair/assessment/morning-reading/check')" class="btn-main">查询检查记录</div>-->
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
        <table class="table">
          <thead>
          <tr>
            <th>学科</th>
            <th>年级</th>
            <th>班级</th>
            <th>表扬次数</th>
            <th>特别表扬次数</th>
            <th>总表扬次数</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,key) in table.list">
            <td>{{item.courseName}}</td>
            <td>{{item.gradeName}}</td>
            <td>{{item.className}}</td>
            <td>{{item.praiseNum}}</td>
            <td>{{item.specialPraiseNum}}</td>
            <td>{{item.praiseTotal}}</td>
          </tr>
          </tbody>
        </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <el-dialog title="导入" class="dialog" :visible.sync="dialog" width="540px">
        <div class="item">
          <span class="title imp">周</span>
          <el-select v-model="form.weekId" default-first-option class="input" clearable filterable>
            <el-option v-for="item in searchData[3].option"
                       :label="item.label" :value="item.value" :key="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="title imp">科目</span>
          <el-select default-first-option v-model="form.courseId" class="input" clearable filterable>
            <el-option v-for="item in searchData[2].option"
                       :label="item.label" :value="item.value" :key="item.value"
            ></el-option>
          </el-select>
        </div>
        <div v-if="dialog" class="item">
          <span class="title imp">上传文件</span>
          <el-input type="file" class="input file-input" ref="upload"
                    accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                    placeholder="请选择"></el-input>
          <a download href="../gy-files/template/晨读检查导入模板.xlsx" class="f-small main-c ml5">下载模板</a>
        </div>
        <div class="btns">
          <div @click="impFun" class="btn-submit btn-upload mr15">确认</div>
          <div @click="dialog = false" class="btn-clear">返回</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        nav: [{name: "晨读检查", path: ""}],
        searchData: [
          {type: 'select', title: '学期', width: '240px', notRemove: true, model: 'termId', option: []},
          {type: 'select', title: '年级', notRemove: true, model: 'gradeId', option: []},
          {type: 'select', title: '学科', notRemove: true, model: 'courseId', option: []},
          {type: 'select', title: '周', model: 'weekId', option: []},
        ],
        dialog: false,
        table: {
          url: '/assessMorningStudy/queryAssessMorningStudyPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            termId: null,
            gradeId: null,
            courseId: null,
            weekId: null
          }
        },
        // 所有的周
        weekList: [],
        form: {
          weekId: null,
          courseId: null,
        }
      }
    },
    methods: {
      // 展现导入弹窗
      showDialog () {
        this.dialog = true
        this.form = {
          weekId: null,
          courseId: null,
        }
      },
      // 导入
      async impFun () {
        if (!this.form.weekId) {
          this.$message.warning('请选择周')
          return
        }
        if (!this.form.courseId) {
          this.$message.warning('请选择科目')
          return
        }
        let file = this.$refs.upload.$refs.input.files[0]
        if (!file) {
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('weekId', this.form.weekId)
        formData.append('courseId', this.form.courseId)
        let obj = {
          url: '/import/importAssessMorningStudy.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          this.$refs.upload.value = null
          this.$remind('导入成功')
          this.dialog = false
          this.$search(this.table)
        }
      },
      // 获取学期
      async getTermList () {
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.searchData[0].option = res.map(e => {
          return {label: e.termName, value: e.schoolTermId}
        })
        res.forEach(e => {
          if (e.isDefault === 'YES') {
            this.table.query.termId = e.schoolTermId
          }
        })
      },
      // 获取周
      async getWeek () {
        let res = await this.$ajax({
          url: '/school/querySchoolTermWeekListWithCurrentTerm.do',
          query:{
            termId: this.table.query.termId
          }
        })
        this.searchData[3].option = []
        res.forEach(e => {
          this.searchData[3].option.push({value: e.id, label: e.weekDesc})
        })
        this.table.query.weekId = res[1].id
      },
      // 获取年级列表
      async getGradeList () {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.searchData[1].option = res.map(e => {
          return {label: e.name, value: e.id}
        })
        this.table.query.gradeId = res[0].id
      },
      // 获取学科
      async subject () {
        let res = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do'
        })
        this.searchData[2].option = res.map(e => {
          return {value: e.id, label: e.name}
        })
        this.table.query.courseId = res[0].id
      },
    },
    watch: {
      'table.query.termId': function () {
        this.getWeek()
      }
    },
    mounted () {
      let p1 = this.getTermList()
      let p2 = this.getGradeList()
      let p3 = this.subject()
      let vm = this
      Promise.all([p1, p2, p3]).then(function (results) {
        vm.$search(vm.table)
      });


    }
  }
</script>
<style lang="sass" scoped>
  .index
    .btn-block
      a
        vertical-align: top
        display: inline-block
        line-height: 30px
        margin-top: 2px
    .dialog
      .title
        width: 70px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .input
        width: calc(100% - 130px)
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .file-input
        /deep/ input
          padding-top: 10px
          line-height: 1
      .item
        margin-bottom: 20px
      .btns
        text-align: center
</style>
